.date {
    background-color: #FFA500;
    border: 1px solid #e0cece;
    display: inline-block;
    min-width: 70px;
    color: black;
    border-radius: 10px;
    font-size: 12px;
    font-weight: bold;
}

.wrapper {
    margin: 0 50px;
}

.clock {
    position: relative;
    font-family: monaco, consolas, "courier new", monospace;
    font-size: 3.5rem;
    line-height: 1.375;
}

.clock span {
    position: relative;
    display: inline-block;
    padding: 0 .25em;
    margin: 0 .06125em;
    z-index: 1;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}

.clock span:first-child {
    margin-left: 0;
}

.clock span:last-child {
    margin-right: 0;
}

.clock span:nth-child(2),
.clock span:nth-child(4) {
    margin-right: .3em;
}

.clock span:nth-child(3),
.clock span:nth-child(5) {
    margin-left: .3em;
}

.clock span:before,
.clock span:after {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    color: #eee;
    text-shadow: 0 1px 0 #fff;
    background: #333;
    border-radius: .125em;
    outline: 1px solid transparent; /* fix jagged edges in ff */
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transition: -webkit-transform .75s, opacity .75s;
    -moz-transition: -moz-transform .75s, opacity .75s;
    transition: transform .75s, opacity .75s;
}

.clock span:before {
    opacity: 1;
    z-index: 1;
    content: attr(data-old);
    -webkit-transform-origin: 0 0;
    -moz-transform-origin: 0 0;
    -ms-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transform: translate3d(0, 0, 0) rotateX(0);
    -moz-transform: translate3d(0, 0, 0) rotateX(0);
    -ms-transform: translate3d(0, 0, 0) rotateX(0);
    transform: translate3d(0, 0, 0) rotateX(0);
}

.clock span:after {
    opacity: 0;
    z-index: 2;
    content: attr(data-now);
    -webkit-transform-origin: 0 100%;
    -moz-transform-origin: 0 100%;
    -ms-transform-origin: 0 100%;
    transform-origin: 0 100%;
    -webkit-transform: translate3d(0, -102.5%, 0) rotateX(90deg);
    -moz-transform: translate3d(0, -102.5%, 0) rotateX(90deg);
    -ms-transform: translate3d(0, -102.5%, 0) rotateX(90deg);
    transform: translate3d(0, -102.5%, 0) rotateX(90deg);
}

.clock .flip:before {
    opacity: 0;
    -webkit-transform: translate3d(0, 102.5%, 0) rotateX(-90deg);
    -moz-transform: translate3d(0, 102.5%, 0) rotateX(-90deg);
    -ms-transform: translate3d(0, 102.5%, 0) rotateX(-90deg);
    transform: translate3d(0, 102.5%, 0) rotateX(-90deg);
}

.clock .flip:after {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0) rotateX(0);
    -moz-transform: translate3d(0, 0, 0) rotateX(0);
    -ms-transform: translate3d(0, 0, 0) rotateX(0);
    transform: translate3d(0, 0, 0) rotateX(0);
}
